:root {
  --bright-blue: #3a7bfd;
  --check-gradient: linear-gradient(130deg, #57ddff, #c058f3);
  --very-light-gray: #fafafa;
  --dark-gray-blue: #9394a5;
  --very-dark-grayish-blue: #484b6a;
  --very-dark-blue: #161722;
  --very-dark-desaturated-blue: #25273c;
  --light-grayish-blue: #cacde8;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-image: url("bg-mobile-light.e1315d1e.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  padding: 1rem 2rem;
  font-family: Josefin Sans, sans-serif;
  font-size: 1.8rem;
}

.header {
  color: #fff;
  letter-spacing: 5px;
}

.container {
  position: relative;
}

.todo-container {
  box-shadow: 0 5px 30px #d3d3d3;
}

.theme-switch {
  --size: 3rem;
  width: var(--size);
  height: var(--size);
  cursor: pointer;
  background-image: url("icon-moon.3850093d.svg");
  background-repeat: no-repeat;
  position: absolute;
  top: 5px;
  right: 0;
}

.container, .todo-container, .buttons-container {
  max-width: 600px;
  margin: 0 auto;
}

.task, .new-task {
  padding: 1.5rem;
}

.tasks, .new-task {
  background-color: #fff;
  border-radius: 5px;
}

.new-task {
  margin-bottom: 3rem;
}

.new-task form {
  align-items: center;
  display: flex;
}

.new-task input {
  border: 0;
  border-bottom: 1px solid var(--light-grayish-blue);
}

.new-task input::placeholder {
  font-size: 1.3rem;
}

.new-task input:focus {
  border-color: var(--bright-blue);
  outline: none;
}

.new-task input:focus::placeholder {
  opacity: .5;
}

.task > * {
  cursor: pointer;
}

.task {
  color: var(--very-dark-grayish-blue);
  border-bottom: 1px solid var(--light-grayish-blue);
  justify-content: space-between;
  align-items: center;
  font-size: 1.5rem;
  display: flex;
}

[type="checkbox"] {
  opacity: 0;
  position: absolute;
}

[type="checkbox"]:checked + label .custom-checkbox {
  background-image: url("icon-check.0ef01779.svg"), var(--check-gradient);
  background-position: center;
  background-repeat: no-repeat;
}

.task label {
  display: flex;
  position: relative;
}

.task label:hover .custom-checkbox, [type="checkbox"]:focus + label .custom-checkbox {
  border-color: var(--bright-blue);
  transform: scale(1.2);
}

label:after {
  content: "";
  height: 1px;
  transform-origin: 0;
  background-color: currentColor;
  transition: all .2s;
  position: absolute;
  top: 45%;
  left: 3rem;
  right: -3px;
  transform: scaleX(0);
}

[type="checkbox"]:checked + label:after {
  transform: scaleX(1);
}

[type="checkbox"]:checked + label {
  opacity: .5;
}

.close {
  width: 2rem;
  height: 2rem;
  background-image: url("icon-cross.49911680.svg");
  background-repeat: no-repeat;
}

.custom-checkbox {
  --spacer: 1.5rem;
  width: var(--spacer);
  height: var(--spacer);
  border: 1px solid var(--light-grayish-blue);
  margin-right: var(--spacer);
  border-radius: 50%;
  transition: transform .25s, border-color .25s;
}

.buttons-container {
  justify-content: space-between;
  padding: 1.5rem;
  display: flex;
  position: relative;
}

.filters {
  text-align: center;
  margin-top: 2rem;
  padding: 1.5rem;
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  box-shadow: 0 5px 30px #d3d3d3;
}

.filters .btn {
  margin: 0 .2rem;
  font-weight: 700;
}

.btn:hover, .btn:focus {
  opacity: 1;
}

.btn:focus {
  outline: 2px solid var(--bright-blue);
  outline-offset: 5px;
  border-radius: 5px;
}

.btn {
  cursor: pointer;
  color: var(--very-dark-grayish-blue);
  opacity: .6;
  background: none;
  border: 0;
  transition: all .2s;
}

.btn.active {
  color: var(--bright-blue);
  opacity: 1;
}

.task-count {
  opacity: .6;
  font-size: 1.3rem;
}

.attribution {
  text-align: center;
  margin-top: 10rem;
  font-size: 11px;
}

.attribution a {
  color: #3e52a3;
}

@media screen and (min-width: 400px) {
  body {
    padding: 2rem;
  }
}

@media screen and (min-width: 768px) {
  body {
    background-image: url("bg-desktop-light.24aa7448.jpg");
  }

  .new-task input::placeholder {
    font-size: 1.5rem;
  }

  .task {
    font-size: 2rem;
  }

  .filters {
    text-align: center;
    box-shadow: none;
    margin: 0;
    padding: 0;
    position: static;
  }

  .btn-delete {
    order: 2;
  }

  .custom-checkbox {
    --spacer: 2.1rem;
  }
}

@media screen and (min-width: 1024px) {
  body {
    padding: 6rem 2rem 0;
  }

  .header {
    font-size: 5rem;
  }

  .theme-switch {
    transform: scale(1.2);
  }

  .close {
    display: none;
  }

  .task:hover .close {
    display: block;
  }

  .attribution {
    margin-top: 8rem;
  }
}

